import { AccessControlList, TargetDrive } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../helpers/i18n/dictionary';
import useImage from '../../hooks/media/useImage';
import usePortal from '../../hooks/portal/usePortal';
import ActionButton from '../Buttons/ActionButton';

const ImageDialog = ({
  title,
  confirmText,
  isOpen,
  acl,
  targetDrive,
  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;

  acl: AccessControlList;
  targetDrive: TargetDrive;

  onConfirm: (imageFileId: string) => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const [, { mutate: saveImage, status }] = useImage();
  const [file, setFile] = useState<File>();

  if (!isOpen) {
    return null;
  }

  const uploadImage = async () => {
    const bytes = new Uint8Array(await file.arrayBuffer());
    saveImage(
      {
        acl: acl,
        bytes: bytes,
        fileId: undefined,
        targetDrive: targetDrive,
      },
      {
        onSuccess: onConfirm,
      }
    );
  };

  const dialog = (
    <div className="relative z-50" aria-labelledby="modal-title" role="dialog" aria-modal="true">
      <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"></div>
      <div className="fixed inset-0 z-10 overflow-y-auto">
        <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
          <div className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all dark:bg-black sm:my-8 sm:w-full sm:max-w-lg">
            <div className="bg-white px-4 pt-5 pb-4 dark:bg-black sm:p-6 sm:pb-4">
              <div className="sm:flex sm:items-start">
                <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    className="mb-3 text-lg font-medium leading-6 text-gray-900 dark:text-slate-50"
                    id="modal-title"
                  >
                    {title}
                  </h3>

                  <input
                    onChange={(e) => {
                      const file = e.target.files[0];
                      if (file) {
                        setFile(file);
                      }
                    }}
                    type="file"
                    accept="image/png, image/jpeg"
                    className={`w-full rounded border border-gray-300 bg-white py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100`}
                  />
                </div>
              </div>
            </div>
            <div className="-mx-2 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
              <ActionButton className="mx-2" onClick={uploadImage} state={status}>
                {confirmText ?? 'Add'}
              </ActionButton>
              <ActionButton className="mx-2" type="secondary" onClick={onCancel}>
                {t('Cancel')}
              </ActionButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return createPortal(dialog, target);
};

export default ImageDialog;
