import { useQuery } from '@tanstack/react-query';
import { ApiType, ChannelDefinition, Client } from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

export interface ChannelDefinitionVm extends ChannelDefinition {
  slug: string;
}

const useChannels = () => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
  const fetchData = async () => {
    const foundChannels = await client.blogDefinitionProvider.getChannelDefinitions();

    return foundChannels.map((channel) => {
      return {
        ...channel,
        slug: convertTextToSlug(channel.name),
      } as ChannelDefinitionVm;
    });
  };

  return [
    useQuery(['channels'], () => fetchData(), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
  ] as const;
};

export default useChannels;
