import { PropsWithChildren } from 'react';

interface BaseProps {
  className?: string;
  [key: string]: unknown;
}

export const Button = ({
  className,
  active,
  reversed,
  ...props
}: PropsWithChildren<
  {
    active?: boolean;
    reversed?: boolean;
  } & BaseProps
>) => (
  <button
    {...props}
    className={`${className ?? ''} cursor-pointer px-1 py-2 ${
      reversed
        ? active
          ? 'text-white'
          : 'text-slate-400'
        : active
        ? 'text-black dark:text-white'
        : 'text-slate-200 dark:text-slate-700'
    }`}
  />
);

export const Toolbar = ({ className, ...props }: PropsWithChildren<BaseProps>) => (
  <div
    {...props}
    className={`${
      className ?? ''
    } relative -mx-1 mb-3 flex flex-row flex-wrap border-b border-slate-200 border-opacity-50`}
  />
);
