import { TargetDrive } from '@youfoundation/transit-lib';
import { useRef, useState } from 'react';
import { useIntersection } from '../../hooks/intersection/useIntersection';
import useImage from '../../hooks/media/useImage';

interface ImageProps {
  targetDrive: TargetDrive;
  fileId: string | undefined;
  className?: string;
  alt?: string;
  title?: string;
}

const Image = ({ targetDrive, fileId, className, alt, title }: ImageProps) => {
  const [isInView, setIsInView] = useState(false);
  const imgRef = useRef<HTMLImageElement>(null);
  const [{ data: imageUrl }] = useImage(isInView ? fileId : undefined, targetDrive);

  useIntersection(imgRef, () => {
    setIsInView(true);
  });

  return (
    <img
      src={imageUrl}
      alt={isInView && imageUrl ? alt : ' '}
      className={`${className} ${
        (!isInView || !imageUrl) && 'h-full w-full animate-pulse bg-slate-100'
      }`}
      title={title}
      ref={imgRef}
    />
  );
};

export default Image;
