import {
  BlogArticle,
  BlogConfig,
  BlogContent,
  HomePageConfig,
  SecurityGroupType,
  TargetDrive,
} from '@youfoundation/transit-lib';
import { debounce } from 'lodash-es';
import { useMemo } from 'react';
import { Descendant } from 'slate';
import { t } from '../../../helpers/i18n/dictionary';
import ImageSelector from '../../Form/ImageSelector';
import Input from '../../Form/Input';
import Textarea from '../../Form/Textarea';
import RichTextEditor from '../../RichTextEditor/RichTextEditor';

const BlogFieldsEditor = ({
  blog,
  onChange,
}: {
  blog: BlogContent;
  onChange: (event: { target: { name: string; value: string | Descendant[] } }) => void;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  const article: BlogArticle | undefined =
    blog.type === 'Article' ? (blog as BlogArticle) : undefined;

  const body = Array.isArray(article?.body)
    ? article?.body
    : [
        {
          type: 'paragraph',
          children: [{ text: article?.body ?? '' }],
        },
      ];

  const defaultDrive: TargetDrive = {
    alias: HomePageConfig.BlogMainContentDriveId.toString(),
    type: BlogConfig.DriveType.toString(),
  };

  return (
    <>
      <div className="mb-5">
        <label className="mb-2 block" htmlFor="blog_caption">
          {t('Caption')}
        </label>
        <Input
          id="blog_caption"
          name="caption"
          defaultValue={blog.caption}
          onBlur={debouncedChange}
        />
      </div>

      <div className="mb-5">
        <label className="mb-2 block" htmlFor="blog_image">
          {t('Primary image')}
        </label>
        <ImageSelector
          id="blog_image"
          name="primaryImageFileId"
          defaultValue={blog.primaryImageFileId}
          onChange={debouncedChange}
          targetDrive={defaultDrive}
          acl={{ requiredSecurityGroup: SecurityGroupType.Anonymous }}
        />
      </div>

      {blog.slug ? (
        <div className="mb-5">
          <label className="mb-2 block" htmlFor="blog_slug">
            {t('Slug [raw & readonly]')}
          </label>
          <Input id="blog_slug" name="slug" defaultValue={blog.slug} disabled />
        </div>
      ) : null}

      {article ? (
        <>
          <div className="mb-5">
            <label className="mb-2 block" htmlFor="blog_abstract">
              {t('Abstract')}
            </label>
            <Textarea
              id="blog_abstract"
              name="abstract"
              defaultValue={article.abstract}
              onChange={debouncedChange}
            />
          </div>

          <RichTextEditor
            defaultValue={body as Descendant[]}
            placeholder={t('Start writing...')}
            onChange={(value) => {
              onChange({ target: { name: 'body', value: value } });
            }}
          />
        </>
      ) : null}
    </>
  );
};

export default BlogFieldsEditor;
