import { BlogContent, BlogPostFile } from '@youfoundation/transit-lib';
import { Descendant } from 'slate';
import BlogFieldsEditor from '../BlogFieldsEditor/BlogFieldsEditor';

const BlogEditor = ({
  blog,
  saveBlog,
}: {
  blog: BlogPostFile<BlogContent>;
  saveBlog: (blogFile: BlogPostFile<BlogContent>) => void;
}) => {
  const newData: BlogPostFile<BlogContent> = { ...blog };

  const onChange = (e: { target: { name: string; value: string | Descendant[] } }) => {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (newData.content as any)[e.target.name] = e.target.value;

    if (newData.content.caption.length) {
      saveBlog(newData);
    }
  };

  return <BlogFieldsEditor blog={blog.content} onChange={onChange} />;
};

export default BlogEditor;
